@import '~swiper/swiper-bundle.min.css';
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
  list-style: none;
}

a[class] {
  text-decoration: none;
  cursor: pointer;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

input:focus,
button:focus,
textarea:focus,
select:focus {
  outline: none;
}

html,
body {
  font: 400 1rem "Lato", sans-serif;
}
@media (max-width: 1400px) {
  html,
body {
    font-size: 14px;
  }
}
@media (max-width: 720px) {
  html,
body {
    font-size: 12px;
  }
}

p, span {
  color: #4c4c4c;
  margin-bottom: 0.9375rem;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Yeseva One", cursive;
  font-weight: 400;
}

h2 {
  font-size: 5.625rem;
}
@media (max-width: 1400px) {
  h2 {
    font-size: 5rem;
  }
}
@media (max-width: 960px) {
  h2 {
    font-size: 5rem;
  }
}
@media (max-width: 720px) {
  h2 {
    font-size: 4.375rem;
  }
}
@media (max-width: 540px) {
  h2 {
    font-size: 3.75rem;
  }
}

h3 {
  font-size: 2.25rem;
}

h4 {
  font-size: 1.3125rem;
}

h5 {
  font-size: 1.3125rem;
}

.menu-toggle {
  display: none;
  width: 35px;
  height: 35px;
  cursor: pointer;
  padding: 15px 5px;
  z-index: 150;
}
@media (max-width: 960px) {
  .menu-toggle {
    display: block;
  }
}
.menu-toggle--active-menu-toggle .menu-toggle__line {
  width: 0;
  background-color: #cc9900;
}
.menu-toggle--active-menu-toggle .menu-toggle__line::before {
  top: 0;
  left: 0;
  transform: rotate(-45deg);
}
.menu-toggle--active-menu-toggle .menu-toggle__line::after {
  top: 0;
  left: 0;
  transform: rotate(45deg);
}
.menu-toggle__line {
  position: relative;
  display: block;
  width: 25px;
  height: 2px;
  background-color: white;
  transition: 0s;
}
.menu-toggle__line::before, .menu-toggle__line::after {
  content: "";
  position: absolute;
  right: 0;
  width: 25px;
  height: 2px;
  background-color: inherit;
  transition: all 0.3s ease-in-out;
}
.menu-toggle__line::before {
  top: -8px;
}
.menu-toggle__line::after {
  top: 8px;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 14.25rem;
  padding: 15px 5px;
  border: 1px solid white;
  margin: 0 15px 15px;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.25);
  font-size: 1.12rem;
  text-transform: uppercase;
  color: white;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.button:hover {
  background-color: rgba(0, 0, 0, 0.7);
  border: 1px solid rgba(0, 0, 0, 0.7);
}
.button--color {
  width: 12.5rem;
  padding: 7px 5px;
  border: 1px solid #c89f2d;
  background-color: #c89f2d;
  text-transform: capitalize;
  font: 400 1rem "Open Sans", sans-serif;
  margin: 10px 0 0;
}
.button--color:hover {
  background-color: rgba(204, 153, 0, 0.6);
  border: 1px solid rgba(204, 153, 0, 0.6);
}

.arrow-up {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 53px;
  height: 53px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.25);
  background-color: #cc9900;
  border-radius: 100%;
  z-index: 10;
  transition: 0.3s ease-in-out;
  position: fixed;
  right: 53px;
  bottom: 60px;
}
@media (max-width: 720px) {
  .arrow-up {
    right: 33px;
    bottom: 30px;
  }
}
.arrow-up:hover {
  background-color: rgba(204, 153, 0, 0.6);
}
.arrow-up__icon {
  width: 17px;
  height: 25px;
  fill: white;
}
.arrow-up--none {
  opacity: 0;
}

