}
@media (max-width: 960px) {
  .main-header__menu {
    justify-content: flex-start;
    flex-direction: column;
    width: 250px;
    height: 100vh;
    position: absolute;
    top: 0;
    padding: 50px 15px 15px;
    left: -200%;
    background-color: #191919;
  }
  .main-header__menu .main-header__link {
    padding: 1.5625rem 1rem;
  }
}
.main-header__menu--active-menu-toggle {
  left: 0;
}
.main-header__link {
  display: flex;
  justify-content: center;
  align-items: center;
  font: 700 1rem "Gentium Book Basic", serif;
  font-size: 1.3rem;
  color: #ffff;
  white-space: nowrap;
  padding: 2.5rem 1rem;
  transition: 0.3s ease-in-out;
  position: relative;
}
.main-header__link::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 1%;
  height: 3px;
  background-color: white;
  opacity: 0;
  transition: 0.3s ease-in-out;
}
.main-header__link:hover::after {
  opacity: 1;
